button, .button {
  border-radius: 5px;
  display: inline-block;
  padding: 8px 12px;
  border: none;
  font-size: .8125rem;
  line-height: 1.125;
  color: var(--pure-white);
  background-color: var(--wai-green);
  text-decoration: none;
  font-weight: bold;
  &:hover, &:focus {
    background-color: var(--w3c-blue);
  }
  &:disabled {
    opacity: .75;
  }
  &-nobg {
    background: transparent;
    &:hover, &:focus {
      background-color: transparent;
      outline: 1px solid #fff;
    }
  }
}
